<script>
import Layouts from '@/components/Layouts-v2.vue'
export default {
  components: { Layouts },
  data () {
    return {
      active_tab: 0,
      options: [
        { label: 'üïô Êó∂Èó¥ÁªÑ‰ª∂', to: '/lib/time', id: 0, disabled: false },
        { label: 'üí™ ÊïàÁéáÁªÑ‰ª∂', to: '/lib/eff', id: 1, disabled: false },
        { label: 'üîß ÂÖ∂‰ªñÁªÑ‰ª∂', to: '/lib/others', id: 2, disabled: false }
        // { label: "üü∞ Notion ÂÖ¨Âºè", to: "/lib/formula", id: 3, disabled: true },
      ]
    }
  },
  computed: {
    activeIndex () {
      let name = this.$route.name
      return `/lib/${name}`
    }
  },
  mounted () {
    // this.$notify({
    //   title: '‚ö†Ô∏è Ê≥®ÊÑè',
    //   message: 'ÊâÄÊúâÁªÑ‰ª∂ÂíåÊ®°ÁâàÈÉΩüà≤Ô∏è‰∫åÊîπüà≤Ô∏è‰∫å‰º†Ôºå‰∫§‰Ωú‰∏öËØ∑Ê†áÊ≥®Êù•Ê∫êÔºåË∞¢Ë∞¢ÔºÅ',
    //   type: 'warning',
